@import '../modals';

.container {
  @extend %modal_container;

  .modal {
    @extend %modal_content;

    input[type=text] {
      @extend %main_input_field_style;
    }

    .error_block {
      @extend %error_block_style
    }

    .buttons_block {
      @extend %buttons_block_style
    }
  }
}